import {useParams} from "react-router-dom";
import {useEffect, useRef, useState} from "react";
import {useAppDispatch, useAppSelector} from "hooks/redux";
import useFetching from "hooks/useFetching";
import useObserver from "hooks/useObserver";
import {collectionsReducer} from "context/collectionsReducer/reducer";
import axios from "axios";

const SavedPostsFetchLogic = () => {
  const params = useParams();
  const [id] = useState<number>(typeof params.id === "string" && params.id !== "all-posts" ? Number(params.id) : 0);
  const collections = useAppSelector(state => state.collectionsReducer);
  const collection = collections.find(item => {
    return item.id === id;
  })!

  const posts = collection?.posts.posts;
  const [page, setPage] = useState<number>(Math.floor(posts?.length / 12));
  const hasNextPage = collection?.posts.hasNextPage;
  const dispatch = useAppDispatch();
  const lastElement = useRef<any>();

  const [fetchPosts, isFetching, error]: any = useFetching(async () => {
    const formData = new FormData();
    const { addPosts, setHasNextPage } = collectionsReducer.actions;
    formData.append("collection_id", String(id));
    formData.append("page", String(page));

    const res = await axios.post("http://127.0.0.1:8000/api/collection/get", formData);
    const data = res.data;

    dispatch(addPosts({id: id, posts: data.posts}));

    if (!data.hasNextPage) {
      dispatch(setHasNextPage({id: id, value: data.hasNextPage}));
    }
  });

  useObserver(lastElement, isFetching, hasNextPage, () => {
    setPage(prev => prev + 1);
  });

  useEffect(() => {
    fetchPosts();
  }, [page]);

  return {
    posts,
    isFetching,
    hasNextPage,
    lastElement
  }
}

export default SavedPostsFetchLogic;
