import React, {FC, useEffect, useRef, useState} from "react";
import axios from "axios";
import CheckIcon from "components/icons/CheckIcon";
import {useAppDispatch, useAppSelector} from "hooks/redux";
import {useParams} from "react-router-dom";
import {collectionsReducer} from "context/collectionsReducer/reducer";
import {LoaderIcon} from "components/icons/LoaderIcon";
import useFetching from "hooks/useFetching";
import useObserver from "hooks/useObserver";
import styles from "./AddModal.module.scss";

interface props {
  setModalName: (value: string) => void
}

const AddModal: FC<props> = ({ setModalName }) => {
  const params = useParams();
  const [id] = useState<number>(typeof params.id === "string" && params.id !== "all-posts" ? Number(params.id) : 0);
  const [selectedPosts, setSelectedPosts] = useState<number[]>([]);
  const body = useRef<any>();
  const dispatch = useAppDispatch();

  const currentCollection = useAppSelector(state => state.collectionsReducer.find(item => {
    return item.id === Number(id);
  }))!;

  const MainCollection = useAppSelector(state => state.collectionsReducer.find(item => {
    return item.id === 0;
  }))!;

  const AllPosts = MainCollection?.posts.posts;

  const [page, setPage] = useState<number>(Math.floor(AllPosts?.length / 12));
  const hasNextPage = MainCollection?.posts.hasNextPage;
  const { setPosts, setImage } = collectionsReducer.actions;
  const lastElement = useRef<any>();

  useEffect(() => {
    let result: number[] = [];

    for (let item of currentCollection?.posts.posts) {
      result.push(item.id);
    }

    setSelectedPosts(result);
  }, []);

  const [fetchPosts, isFetching, error]: any = useFetching(async () => {
    const formData = new FormData();
    const { addPosts, setHasNextPage } = collectionsReducer.actions;
    formData.append("collection_id", String(0));
    formData.append("page", String(page));

    const res = await axios.post("http://127.0.0.1:8000/api/collection/get", formData);
    const data = res.data;

    dispatch(addPosts({id: 0, posts: data.posts}));

    if (!data.hasNextPage) {
      dispatch(setHasNextPage({id: 0, value: data.hasNextPage}));
    }
  });

  useObserver(lastElement, isFetching, hasNextPage, () => {
    setPage(prev => prev + 1);
  });

  useEffect(() => {
    fetchPosts();
  }, [page]);

  const selectPost = (id: number) => {
    if (selectedPosts.indexOf(id) === -1) {
      setSelectedPosts([...selectedPosts, id]);
    } else {
      setSelectedPosts(selectedPosts.filter((item: any) => {return item !== id}));
    }
  }

  const done = async () => {
    const result = AllPosts.filter(post => {
      return selectedPosts.indexOf(post.id) !== -1;
    }).reverse();

    const image = result[0]?.images[0] ?? ""

    dispatch(setPosts({id: Number(id), posts: result}));
    dispatch(setImage({collectionId: Number(id), image: image}))
    setModalName("");

    const formData = new FormData();
    formData.append("collection_id", String(id));
    formData.append("posts_id", String(selectedPosts));

    await axios.post("http://127.0.0.1:8000/api/collection/edit", formData);
  }

  return (
    <div className={styles.modal}>
      <div className={styles.header}>Добавить из "Сохрнанённого"</div>
      <div className={styles.body} ref={body}>
        {AllPosts &&
          AllPosts.map((post: any, index: number) => {
            return <div
              className={styles.post}
              style={{"backgroundImage": `url(http://127.0.0.1:8000${post.images[0]})`}}
              key={index}
              onClick={() => selectPost(post.id)}
            >
              <div className={selectedPosts.indexOf(post.id) !== -1 ? styles.post_wrapper_active : styles.post_wrapper}>
                {selectedPosts.indexOf(post.id) !== -1 &&
                  <CheckIcon />
                }
              </div>
            </div>
          })
        }
      </div>
      {hasNextPage && AllPosts.length > 0 &&
        <div className={styles.loader} ref={lastElement}>
          <LoaderIcon />
        </div>
      }
      <div className={styles.footer}>
        <div className={styles.option} onClick={() => setModalName("MainModal")}>Назад</div>
        <div className={styles.separator} />
        <div className={styles.option_next} onClick={() => done()}>Готово</div>
      </div>
    </div>
  );
}

export default AddModal;
